ul.nav {
    margin: 0;
    padding: 0;
    width: 100%;
    background-color: #000;
    height: 100%;
    overflow: auto;
  }
  ul.nav li a {
    display: block;
    color: #fff;
    padding: 8px 16px;
    text-decoration: none;
    float: left;
    font-weight: bold;
  }
  ul.nav li a.active {
    background-color: #4CAF50;
    color: white;
  }
  ul.nav li a:hover {
    background-color: #fff;
    color: #000;
    border-style: double;
    text-decoration: none;
  }
  .active {
    background-color: #4CAF50;
  }
  
  @media screen and (max-width: 900px) {
    ul {
      width: 100%;
      height: auto;
      position: relative;
    }
    ul.nav li a {
      float: left;
      padding: 8px;
    }
  }
  
  @media screen and (max-width: 400px) {
    ul.nav li a {
      width: 100%;
      text-align: center;
      color: black;
      border-style: double;
      padding: 0.5rem 1rem;
    }
    ul.nav{
      background-color: #fff;
    }
    ul.nav li a:hover {
      background-color: #000;
      color: #fff;
      border-style: double;
      text-decoration: none;
    }
  }