@import url(https://fonts.googleapis.com/css?family=Kalam&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.address {
    text-align: center;
    width: 25%;
    height: 40%;
    margin: 0 auto;
    background-color: black;
    color: azure;
    float: left;
    border: 5px solid white;
    padding-top:  1%;
    margin-top: 100px;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.9);
    font-family: 'Kalam', cursive;
    font-size: 25px;
}
.address-header {
    text-align: center;
    margin-top: 30px;
    font-size: 50px;
    color: #444;
    box-shadow: 0 0 0 3px rgba(255, 0,0, 0.5);
}
.search-form {
    text-align: center;
    margin-top: 30px;
}
.btn-search{
   border: 1px solid black;
   width: 10%;
   background-color: lightgray;
   
}
.search-input {
    width: 20%;
}
@media screen and (max-width: 400px) {
    .address {
        width: 100%;
    }
    .search-form {
        text-align: center;
        margin-top: 30px;
    }
    .btn-search{
       border: 1px solid black;
       width: 20%;
       background-color: lightgray;
       
    }
    .search-input {
        width: 40%;
        border: 1px solid black;
    }
}
.main {
  background-image: url(/address-diary-react-client/static/media/property.232361ac.jpg);
  background-size: cover;
}
.red:after { 
  content:" *";
  color:red; 
}
.home-page-link:link,
.home-page-link:visited {
  display: block;
  height: 6vh;
  margin-left: 20px;
  padding: 0.5rem;
  border-radius: 5px;
  text-decoration: none;
  -webkit-transition: all .2s;
  transition: all .2s;
  font-size: 1rem;
  border: 1px solid black;
  color:black;
  width: 11rem;
  text-align: center;
  padding-top: 15px;
}
.home-page-link:hover {
  border-style: ridge;
  background-color: #000;
  color: #fff;
}
.form-container {
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 5%;
  height: 100%;
}
.label {
  font-weight: 800;
  font-size: 20px;
  color: white;
}
.input {
  background-color: #000;
  color: #fff;
}
.h2-header {
  text-align: center;
  color: black;
  padding-top: 5%;
  font-size: 40px;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  font-weight: bold;
}

@media screen and (max-width: 400px) {
  .h2-header {
    font-size: 30px;
  }
  .main {
    background-image: none;
  }
  .input {
    background-color: #fff;
    color: #000;
  }
  .label {
    font-weight:normal;
    font-size: 20px;
    color: #000;
  }
  .form-container {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10%;
    height: 100%;
  }
}

    

    
ul.nav {
    margin: 0;
    padding: 0;
    width: 100%;
    background-color: #000;
    height: 100%;
    overflow: auto;
  }
  ul.nav li a {
    display: block;
    color: #fff;
    padding: 8px 16px;
    text-decoration: none;
    float: left;
    font-weight: bold;
  }
  ul.nav li a.active {
    background-color: #4CAF50;
    color: white;
  }
  ul.nav li a:hover {
    background-color: #fff;
    color: #000;
    border-style: double;
    text-decoration: none;
  }
  .active {
    background-color: #4CAF50;
  }
  
  @media screen and (max-width: 900px) {
    ul {
      width: 100%;
      height: auto;
      position: relative;
    }
    ul.nav li a {
      float: left;
      padding: 8px;
    }
  }
  
  @media screen and (max-width: 400px) {
    ul.nav li a {
      width: 100%;
      text-align: center;
      color: black;
      border-style: double;
      padding: 0.5rem 1rem;
    }
    ul.nav{
      background-color: #fff;
    }
    ul.nav li a:hover {
      background-color: #000;
      color: #fff;
      border-style: double;
      text-decoration: none;
    }
  }
